@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');

* {
    box-sizing: border-box;
  }
  *:after {
    box-sizing: border-box;
  }
  *:before {
    box-sizing: border-box;
  }

  
  
  body {
    margin: 0;
    padding: 0;
    font-family: "NanumSquare";
    color: #2a3f47;
  }
  body a {
    text-decoration: none;
    color: #101010;
  }
  body header {
    width: 100%;
    height: 80px;
    line-height: 80px;
    border-bottom: 1px solid #eaeaea;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 99;
  }
  body header .header {
    width: 80%;
    margin: 0 auto;
    position: relative;
  }
  body header .header .inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  body header .header .inner .logo {
    line-height: 10px;
  }
  body header .header .inner .logo a {
    display: inline-block;
  }
  body header .header .inner .logo a img {
    width: 100%;
  }
  body header .header .inner .icon {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100px;
    height: 80px;
    line-height: 10px;
    gap: 10px;
  }
  body header .header .inner .icon ul {
    width: 100%;
  }
  body header .header .inner .icon ul img {
    width: 100%;
  }
  body header .header .allmenu {
    position: absolute;
    top: 0;
    left: 15%;
    width: 100%;
  }
  body header .header .allmenu .menuwidth {
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
  }
  body header .header .allmenu .menuwidth li {
    width: 100%;
  }
  body header .header .allmenu .menuwidth li a {
    color: #101010;
    font-weight: 600;
  }
  body header .header .allmenu .sub {
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
    width: 70%;
  }
  body header .header .allmenu .sub .subbox {
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: flex-start;
    margin-top: 15px;
  }
  body header .header .allmenu .sub .subbox ul {
    width: 100%;
    font-weight: 600;
    font-size: 0.9rem;
  }
  body header .header .allmenu .sub .subbox ul li {
    font-size: 0.85rem;
    text-align: center;
    line-height: 40px;
    margin: 0;
    padding: 0;
  }
  body header .header .allmenu .sub .subbox ul li ul {
    display: none;
  }
  body header .header .allmenu .sub .subbox ul li ul li {
    font-size: 0.8rem;
  }
  body header .header .allmenu .sub .subbox ul li ul li a {
    color: #cdcdcd;
  }
  body header .header .allmenu .sub .subbox ul li ul li a.on {
    color: #2a3f47;
    font-weight: 800;
  }
  body header .background {
    position: absolute;
    top: 80px;
    left: 0;
    background: #fff;
    width: 100%;
    height: 300px;
    display: none;
  }
  body .sub {
    width: 100%;
