  body .new-product {
    width: 100%;
    background: url(../public/img/greenbanner.jpeg) no-repeat center 420px;
  }
  body .new-product .newwidth {
    width: 70%;
    margin: 0 auto;
    text-align: center;
  }
  body .new-product .newwidth .boxgrid {
    display: grid;
    margin: 60px auto;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 10px;
  }
  body .new-product .newwidth .boxgrid img {
    width: 100%;
    height: 100%;
  }
  body .new-product .newwidth .boxgrid .r-box {
    grid-column: 1/3;
    grid-row: 1/3;
    cursor: pointer;
  }
  body .new-product .newwidth .boxgrid .r-box ul {
    font-size: 4.5rem;
    font-weight: 600;
    text-align: left;
    margin: 10px auto;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) {
    font-size: 1.7rem;
    margin-bottom: 0;
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: flex-start;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) span {
    font-weight: 900;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) .arrow-wrap {
    width: 50px;
    height: 10px;
    display: flex;
    align-items: center;
    margin-left: -20px;
    margin-top: 14px;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) .arrow-wrap .slide-arrow {
    width: 11px;
    height: 1px;
    position: relative;
    margin-left: -11px;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) .arrow-wrap .slide-arrow::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: #555354;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(45deg);
    transform-origin: right;
  }
  body .new-product .newwidth .boxgrid .r-box ul:nth-child(3) .arrow-wrap .slide-line {
    width: 100%;
    height: 1px;
    background-color: #555354;
  }
